import React from 'react'
import { StaticImage } from 'gatsby-plugin-image';
import { Element } from 'react-scroll'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Container from '../../../components/Container'
import theme, { responsive, Media } from '../../../components/ThemeProvider/theme';


const CircleContent = ({ title, desc, text, index }) => (
  <Box.Absolute
    width="60%"
    top={responsive('15%', '50%')}
    left={responsive(index ? 'unset' : '32.5%', '50%')}
    right={responsive(index ? '32.5%' : 'unset', '50%')}
    transform={responsive('none', 'translate(-50%, -50%)')}
    color="white"
    textAlign="center"
    fontSize={responsive('0.75em', '1em')}
  >
    <Text.Bold fontSize="1.5em">{title}</Text.Bold>
    <Box.Inline fontWeight="bold" borderTop="1px solid" pt="0.5rem" mt="0.25rem" fontSize="1.5em">{desc}</Box.Inline>
    <Text fontSize="1em">{text}</Text>
  </Box.Absolute>
)

const content = [
  {
    title: "焦點1",
    desc: "ESG綠色永續",
    text: "全面綠化成為全球趨勢",
    img: <StaticImage src="../left.png" alt="left" />
  },
  {
    title: "焦點2",
    desc: "新興市場",
    text: "收益表現更加亮眼",
    img: <StaticImage src="../right.png" alt="right" />
  },
]

const KeyPoint = () => {
  return (
    <Element name="keyPoint">
      <Box position="relative" mt={`-${theme.headerHeight}`} overflow="hidden">
        <div>
          <Media lessThan="tablet">
            <StaticImage
              src="cover_mobile.jpg"
              alt="手機版背景圖"
            />
          </Media>
          <Media greaterThanOrEqual="tablet">
            <StaticImage
              src="cover_desk.jpg"
              alt="電腦版背景圖"
            />
          </Media>
        </div>
        {content.map(({ title, desc, text, img }, k) => (
          <Box.Absolute
            width={responsive('90%', '20%')}
            bottom={responsive('0%', '50%')}
            left={!k && responsive('0%', '2.5%')}
            right={k && responsive('0%', '2.5%')}
            transform={responsive(k ? 'translate(40%, 50%)' : 'translate(-40%, 50%)', 'translate(0, 50%)')}
            key={k}
          >
            {img}
            <CircleContent index={k} title={title} desc={desc} text={text} />
        </Box.Absolute>
        ))}
        <Container position="absolute" top="12.5%" left="50%" width="100%" textAlign="center" transform="translateX(-50%)">
          <Text.H1 fontSize={responsive('1.5em', '2.25em')} fontWeight="bold" color="custom.orange">兩大關鍵 投資焦點</Text.H1>
        </Container>
      </Box>
    </Element>
  )
}

export default KeyPoint
